.rmdb-header {
  width: 100%;
  height: auto;
  background: #1c1c1c;
  padding: 0 20px;
  box-sizing: border-box;
}

.rmdb-header-content {
  max-width: 1280px;
  min-height: 120px;
  height: auto;
  padding: 20px 0px;
  box-sizing: border-box;
  overflow: hidden;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
}

.rmdb-logo {
  width: 300px;
  margin-top: 20px;
  float: left;
}

@media screen and (max-width: 500px) {
  .rmdb-header-content {
    max-width: 1280px;
    min-height: 0px;
  }

  .rmdb-logo {
    width: 170px;
    margin-top: 5px;
  }
}
